html {
  scroll-behavior: smooth;
}

body {
  background: var(--color-bg);
}

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
  font-family: "Roboto", sans-serif;
  color: var(--color-text);
}

img {
  max-width: 100%;
}

input,
button {
  border: none;
  background: transparent;
  cursor: pointer;
}

.is_outer {
  position: relative !important;
  left: -9999px !important;
}

.is_hidden {
  display: none !important;
}

.for_mobile {
  display: none;
}
@media (max-width: 1024px) {
  .for_mobile {
    display: block;
  }
}

.error_ {
  border: 2px solid #ff4019 !important;
}

.visualy_hidden {
  position: absolute !important;
  width: 1px !important;
  height: 1px !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  overflow: hidden;
}

.container {
  max-width: 76.5625vw;
  padding: 0 0.78125vw;
  margin: 0 auto;
}

.header {
  padding: 0.5208333333vw 0;
  height: 3.6458333333vw;
}

.header__container {
  display: flex;
  align-items: center;
}

.header__logo {
  width: 2.6041666667vw;
  height: 2.8645833333vw;
  margin-right: 3.125vw;
}

.nav__list {
  display: flex;
  align-items: center;
}

.nav__item {
  width: auto;
  height: auto;
}
.nav__item:not(:last-child) {
  margin-right: 3.125vw;
}

.nav__link {
  font-weight: 900;
  font-size: 0.9375vw;
  line-height: 100%;
  color: var(--color-header-text);
}

.header__btn {
  margin-left: auto;
}

.hero {
  height: 37.5vw;
  background: var(--color-header-text);
}

.product {
  padding: 2.34375vw 0;
  padding-top: 4.6875vw;
}

.product__cards {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  grid-gap: 2.6041666667vw;
  height: 39.0625vw;
}

.product__card {
  position: relative;
  padding: 2.0833333333vw;
  padding-bottom: 6.5625vw;
  background: var(--color-card);
  border-radius: 0.78125vw;
}
.product__card:nth-child(1) {
  padding-bottom: 6.7708333333vw;
  grid-column: 1/3;
}
.product__card:nth-child(1) .product__btn {
  padding: 0.8333333333vw 1.4583333333vw;
  color: var(--color-text);
}

.product__btn {
  position: absolute;
  left: 2.0833333333vw;
  bottom: 2.0833333333vw;
}

.about {
  padding: 2.34375vw 0;
}

.about__desc {
  max-width: 43.2291666667vw;
}

.faq {
  padding: 2.34375vw;
}

#accordion {
  width: 39.0625vw;
}

.accordion__heading {
  cursor: pointer;
}

.accordion__heading,
.accordion__desc {
  padding: 0.8333333333vw 0;
}

.accordion__heading.ui-state-active {
  border-bottom: 0.0520833333vw solid var(--color-text);
}

.accordion__num {
  font-style: italic;
  font-weight: 900;
  font-size: 1.875vw;
  line-height: 150%;
  margin-right: 2.6041666667vw;
}

.footer {
  height: 3.6458333333vw;
  background: var(--color-bg-footer);
}

.footer__container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.up-heading {
  display: block;
  font-weight: 700;
  font-size: 0.7291666667vw;
  line-height: 100%;
  letter-spacing: 0.25em;
  text-transform: uppercase;
  color: var(--color-desc);
  margin-bottom: 0.5208333333vw;
}

.heading {
  font-size: 1.875vw;
  line-height: 100%;
  margin-bottom: 2.0833333333vw;
}

.desc {
  font-weight: 400;
  font-size: 1.25vw;
  line-height: 150%;
  color: var(--color-desc);
}

.btn_tg {
  font-weight: 500;
  font-size: 0.9375vw;
  line-height: 100%;
  color: var(--color-main);
  padding: 0.5208333333vw 1.4583333333vw 0.5729166667vw 1.4583333333vw;
  border: 0.0520833333vw solid var(--color-main);
  border-radius: 0.5208333333vw;
}

.btn {
  padding: 0.7291666667vw 1.4583333333vw;
  background: var(--color-btn);
  border-radius: 0.2604166667vw;
  font-weight: 500;
  font-size: 0.9375vw;
  line-height: 100%;
  color: var(--color-main);
}